<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品分类</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片区域 -->
    <el-card>
      <el-alert title="添加商品信息" type="info" show-icon center></el-alert>
      <!-- 步骤条区域 -->
      <el-steps :active="activeIndex - 0" finish-status="success" align-center>
        <el-step title="基本信息"></el-step>
        <el-step title="动态属性"></el-step>
        <el-step title="静态属性"></el-step>
        <el-step title="商品图片"></el-step>
        <el-step title="商品内容"></el-step>
        <el-step title="完成"></el-step>
      </el-steps>
      <!-- tabls标签页 -->
      <el-form
        :model="addFormData"
        :rules="addFormRules"
        ref="addFormRef"
        label-width="100px"
        label-position="top"
      >
        <el-tabs
          tab-position="left"
          v-model="activeIndex"
          :before-leave="beforeTabLeave"
          @tab-click="tabClicked"
        >
          <el-tab-pane label="基本信息" name="0">
            <!-- 表单项 -->
            <!-- 名称 -->
            <el-form-item label="商品名称" prop="goods_name">
              <el-input v-model="addFormData.goods_name"></el-input>
            </el-form-item>
            <!-- 价格 -->
            <el-form-item label="商品价格" prop="goods_price">
              <el-input v-model="addFormData.goods_price"></el-input>
            </el-form-item>
            <!-- 重量 -->
            <el-form-item label="商品重量" prop="goods_weight">
              <el-input v-model="addFormData.goods_weight"></el-input>
            </el-form-item>
            <!-- 数量 -->
            <el-form-item label="商品数量" prop="goods_number">
              <el-input v-model="addFormData.goods_number"></el-input>
            </el-form-item>
            <!-- 分类 -->
            <el-form-item label="商品分类" prop="goods_cat">
              <el-cascader
                v-model="addFormData.goods_cat"
                :options="cateList"
                :props="{ expandTrigger: 'hover',label:'cat_name',value:'cat_id'}"
                @change="handleChange"
              ></el-cascader>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="动态属性" name="1">
            <el-form-item v-for="item in manyTableData" :key="item.attr_id" :label="item.attr_name">
              <!-- 多选框 -->
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox :label="cb" v-for="(cb,i) in item.attr_vals" :key="i" border></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="静态属性" name="2">
            <el-form-item
              :label="item1.attr_name"
              v-for="item1 in onlyTableData"
              :key="item1.attr_id"
            >
              <el-input v-model="item1.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="商品图片" name="3">
            <!-- action表示图片要上传到的后台API地址
            虽然在main.js里设置了请求头,但这里没使用,要手动添加请求头headers-->
            <el-upload
              :action="uploadBase"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              list-type="picture"
              :headers="headerObj"
              :on-success="handleSuccess"
            >
              <el-button size="small" type="primary">点击上传</el-button>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="商品内容" name="4">
            <!-- 富文本编辑器 -->
            <quill-editor v-model="addFormData.goods_introduce"></quill-editor>
            <el-button type="primary" @click="add">添加商品</el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>
    <!-- 预览图片 -->
    <el-dialog title="图片预览" :visible.sync="previewDialogVisible" width="60%">
      <img :src="previewPath" alt class="previewImg" />
    </el-dialog>
  </div>
</template>
<script>
import _ from 'lodash'
export default {
  data () {
    return {
      // 控制步骤条和tabs标签
      activeIndex: '0',
      // 商品分类列表
      cateList: [],
      // 添加数据
      addFormData: {
        goods_name: '',
        // 以为','分割的分类列表 商品所属的分类数组
        goods_cat: [],
        goods_price: '',
        goods_number: '',
        goods_weight: '',
        // 图片数组
        pics: [],
        // 商品介绍
        goods_introduce: '',
        attrs: []
      },
      // 添加数据规则
      addFormRules: {
        goods_name: [
          { required: true, message: '请输入商品名称', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '请输入商品价格', trigger: 'blur' }
        ],
        goods_weight: [
          { required: true, message: '请输入商品重量', trigger: 'blur' }
        ],
        goods_number: [
          { required: true, message: '请输入商品数量', trigger: 'blur' }
        ],
        goods_cat: [
          { required: true, message: '请选择商品分类', trigger: 'blur' }
        ]
      },
      // 动态参数列表
      manyTableData: [],
      // 静态参数列表
      onlyTableData: [],
      // 上传图片后台地址
      uploadBase: 'http://127.0.0.1:8888/api/private/v1/upload',
      // 上传图片请求头
      headerObj: {
        Authorization: window.sessionStorage.getItem('token')
      },
      // 预览图片路径
      previewPath: '',
      // 预览图片对话框
      previewDialogVisible: false
    }
  },
  methods: {
    // 获取商品分类数据
    async getCateList () {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status != 200) return this.$message.error('获取失败')
      console.log(res.data)
      // 成功

      this.cateList = res.data
    },
    // 级联选择器发生变化触发
    handleChange () {
      if (this.addFormData.goods_cat.length != 3) {
        this.$message.error('只能选三级分类')
        this.addFormData.goods_cat = []
      }
    },
    // 切换tab标签触发
    beforeTabLeave (activeName, oldActiveName) {
      // oldActiveName为要离开的页面  activeName 为要前往的页面
      if (oldActiveName == '0' && this.addFormData.goods_cat.length != 3) {
        this.$message.error('请选择商品分类')
        return false
      }
    },
    // 点击标签触发
    async tabClicked () {
      // 说明进入的是动态参数页面
      if (this.activeIndex == '1') {
        const { data: res } = await this.$http.get(
          `categories/${this.cateId}/attributes`,
          {
            params: { sel: 'many' }
          }
        )
        console.log(res.data)
        if (res.meta.status != 200) return this.$message.error('获取失败')
        // 获取成功
        res.data.forEach(item => {
          item.attr_vals =
            item.attr_vals.length == 0 ? [] : item.attr_vals.split(',')
        })
        this.manyTableData = res.data
      }
      // 说明进入的是静态参数页面
      if (this.activeIndex == '2') {
        const { data: res } = await this.$http.get(
          `categories/${this.cateId}/attributes`,
          {
            params: { sel: 'only' }
          }
        )
        if (res.meta.status != 200) return this.$message.error('获取失败')
        // 成功
        this.onlyTableData = res.data
      }
    },
    // 处理预览图
    handlePreview (file) {
      this.previewPath = file.response.data.url
      this.previewDialogVisible = true
    },
    // 监听删除图片
    handleRemove (file) {
      const filePath = file.response.data.tmp_path
      // 从数组中删除对应的图片
      this.addFormData.pics = this.addFormData.pics.filter(
        item => item.pic != filePath
      )
      console.log(this.addFormData)
    },
    // 监听图片上传成功
    handleSuccess (res) {
      //  1 拼接好一个对象
      const picInfo = { pic: res.data.tmp_path }
      // 2 添加到添加数据中
      this.addFormData.pics.push(picInfo)
      console.log(this.addFormData)
    },
    // 添加商品
    add () {
      // 表单预验证
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return this.$message.error('请填入必填项')
        // 发起添加请求 先深拷贝一份添加数据
        let form = _.cloneDeep(this.addFormData)
        form.goods_cat = form.goods_cat.join(',')
        // 处理动态参数
        this.manyTableData.forEach(item => {
          const newInfo = { attr_id: item.attr_id, attr_value: item.attr_vals.join(' ') }
          this.addFormData.attrs.push(newInfo)
        })
        // 处理静态属性
        this.onlyTableData.forEach(item => {
          const newInfo = {
            attr_id: item.attr_id,
            attr_value: item.attr_vals
          }
          this.addFormData.attrs.push(newInfo)
        })
        form.attrs = this.addFormData.attrs
        // 发送添加商品请求
        const { data: res } = await this.$http.post('goods', form)
        if (res.meta.status != 201) return this.$message.error('添加商品失败')
        // 添加成功
        this.$message.success('添加商品成功')
        // 编程式导航
        this.$router.push('/goods')
      })
    }
  },
  created () {
    this.getCateList()
  },
  computed: {
    cateId () {
      if (this.addFormData.goods_cat.length == 3) {
        return this.addFormData.goods_cat[2]
      } else {
        return null
      }
    }
  }
}
</script>
<style scoped lang="less">
.el-steps {
  margin-top: 16px;
  margin-bottom: 16px;
}
.el-checkbox {
  margin-right: 5px !important;
}
.previewImg {
  width: 100%;
}
.el-button {
  margin-top: 20px;
}
</style>
