<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片区域 -->
    <el-card>
      <el-input placeholder="请输入内容" class="input-with-select" clearable v-model="quertInfo.query" @clear="getGoodsList">
        <el-button slot="append" icon="el-icon-search" @click="getGoodsList"></el-button>
      </el-input>
      <el-button type="primary" @click="toAddGood">添加商品</el-button>
      <!-- 表格区域 -->
      <el-table :data="goodList" style="width: 100%">
        <el-table-column type="index" ></el-table-column>
        <el-table-column prop="goods_name" label="商品名称" width="280px"></el-table-column>
        <el-table-column prop="goods_price" label="商品价格(元)" ></el-table-column>
        <el-table-column prop="goods_weight" label="商品重量" ></el-table-column>
        <el-table-column  label="创建时间">
            <template slot-scope="scope">
                     {{scope.row.add_time | dateFormat}}
            </template>
        </el-table-column>
        <el-table-column  label="操作">
            <template slot-scope="scope">
                <el-button type="primary" icon="el-icon-edit" size="mini"></el-button>
                <el-button type="danger" icon="el-icon-delete" size="mini" @click="delGood(scope.row.goods_id)" ></el-button>
            </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="quertInfo.pagenum"
      :page-sizes="[10, 20, 30, 40]"
      :page-size="quertInfo.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
    </el-card>
  </div>
</template>
<script>
export default {
  data () {
    return {
      // 查询携带的参数
      quertInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      // 商品总量
      total: null,
      // 商品列表
      goodList: []
    }
  },
  methods: {
    async getGoodsList () {
      const { data: res } = await this.$http.get('goods', {
        params: this.quertInfo
      })
      if (res.meta.status != 200) return this.$message.error('获取失败')
      this.goodList = res.data.goods
      this.total = res.data.total
      console.log(res.data.goods)
    },
    // 更改每页数据触发
    handleSizeChange (newsize) {
      this.quertInfo.pagesize = newsize
      // 重新获取数据
      this.getGoodsList()
    },
    // 更改页数触发
    handleCurrentChange (newpagenum) {
      this.quertInfo.pagenum = newpagenum
      // 重新获取数据
      this.getGoodsList()
    },
    // 实现删除商品功能
    delGood (id) {
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        //   发送删除请求
        const { data: res } = await this.$http.delete(`goods/${id}`)
        if (res.meta.status != 200) return this.$message.error('删除失败')
        this.$message.success('删除成功')
        this.getGoodsList()
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    // 跳转到商品添加页
    toAddGood () {
      this.$router.push('/goods/add')
    }

  },
  created () {
    this.getGoodsList()
  }
}
</script>
<style scoped lang="less">
.input-with-select {
  width: 400px;
  margin-right: 30px;
}
</style>
